<template>
  <div id="players">
    <div class="d-flex mb-2 align-items-center">
      <label for="teamSelect" class="text-nowrap m-0 mr-3"> </label>
      <select class="custom-select bg-dark border-dark text-light flex-grow-1" id="teamSelect" v-model="selectedTeam">
        <option selected :value="null"> </option>
        <option v-for="team in teams" :key="'selteam'+team.id" :value="team.id">{{ team.name }}</option>
      </select>
    </div>
    <div class="table-responsive">
      <table class="table table-main">
        <thead>
        <tr class="text-center">
          <th scope="col"></th>
          <th title="">K</th>
          <th title="">D</th>
          <th title="">A</th>
          <th title=" ">FA</th>
          <th title=" ">BP</th>
          <th title=" ">BD</th>
          <th title="">TK</th>
          <th title=" ">TMK</th>
          <th title="-" @click.prevent="kd_divide=!kd_divide">KD</th>
          <th title="   ">ADR</th>
          <th title="  ">FRP</th>
          <th title=" ">HSP</th>
          <th title="">ACES</th>
        </tr>
        </thead>
        <tbody>
        <GridPlayer v-for="player in filteredRating" :key="player.steamid" :kd_divide="kd_divide" v-bind="player"/>
        </tbody>
      </table>
      <p class="text-secondary">: {{ filteredRating.length }}</p>
    </div>
  </div>
</template>

<script>
import GridPlayer from "@/components/molecules/GridPlayer";

export default {
  name: "PlayersPanel",
  data: () => ({
    rating: [],
    teams: [],
    selectedTeam: null,
    kd_divide: false
  }),
  components: {GridPlayer},
  created() {
    this.rating = this.$store.state.player_rating
    this.teams = this.$store.state.teams
    this.unwatch_player_rating = this.$store.watch((state, getters) => getters.player_rating, (rating) => {
      this.rating = rating
    });
    this.unwatch_teams = this.$store.watch((state, getters) => getters.teams, (teams) => {
      this.teams = teams
    });
  },
  computed: {
    filteredRating() {
      if (!this.selectedTeam)
        return this.rating
      return this.rating.filter(it => it.info.team === this.selectedTeam)
    }
  },
  beforeDestroy() {
    this.unwatch_player_rating();
    this.unwatch_teams();
  }
}
</script>

